<template>
  <!-- 全域原件 -->
  <LoadingView :active="isLoading"></LoadingView>
  <div class="container">
    <div class="title mb-5">
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li class="breadcrumb-item">
            <router-link class="text-decoration-none" to="/home">韋恩潛水</router-link>
          </li>
          <li class="breadcrumb-item" aria-current="page">
            <router-link class="text-decoration-none" to="/userProducts">潛水假期</router-link>
          </li>
          <li class="breadcrumb-item active" aria-current="page">潛水課程</li>
        </ol>
      </nav>
      <hr />
    </div>
    <div class="row px-5">
      <div class="col-12 col-md-6 row mainimg g-3">
        <img :src="mainImg" :alt="product.engtitle" :title="product.title" />
        <button
          v-for="(i, index) in product.images"
          :key="i.id"
          class="col-4 propimg border-0"
          @click="pickPic(i, index)"
        >
          <img class="rounded" :src="i" :alt="product.engtitle" :title="product.title" />
          <div class="overlay" :class="{ noneOverlay: index === classIndex }"></div>
        </button>
      </div>
      <div class="col-12 col-md-6 m-auto">
        <h3>{{ product.title }}</h3>
        <p>{{ product.engtitle }}</p>
        <p>{{ product.category }}</p>
        <p style="white-space: pre-wrap">{{ product.description }}</p>
        <div class="text-end">
          <small>
            <s>${{ this.$filters.currency(product.origin_price) }}</s></small
          >
          <br />
          <b class="fs-4">售價$:{{ this.$filters.currency(product.price) }}/人</b>
          <div class="mb-3">
            <button
              class="btn btn-outline-secondary rounded-0"
              min="1"
              @click.prevent="productNum -= 1"
            >
              -</button
            ><label for="number1">
              <input
                name="number1"
                id="number1"
                type="number"
                max="10"
                min="1"
                class="productNumber btn border"
                v-model="productNum"
            /></label>
            <button class="btn btn-outline-secondary rounded-0" @click.prevent="productNum += 1">
              +
            </button>
          </div>
          <div class="like">
            <i
              class="bi"
              :class="isLiked() ? 'bi-heart-fill' : 'bi-heart'"
              @click="liked()"
              @keyup="liked()"
            ></i>
            <button type="button" class="btn btn-titleblue w-50" @click.prevent="updateCart">
              加入購物車
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="mt-5 row justify-content-center bottxt bg-bgblue pt-3">
      <div class="col-5 text-center">
        <h4>課程內容:</h4>
        <p style="white-space: pre-wrap">{{ product.content }}</p>
      </div>
      <div class="col-5 text-center">
        <h4>注意事項:</h4>
        <p style="white-space: pre-wrap">{{ product.precautions }}</p>
      </div>
    </div>
    <h3 class="text-textblue mt-3">你可能有興趣</h3>
    <OtherCard></OtherCard>
  </div>
</template>

<script>
import OtherCard from '@/components/Front/OtherCard.vue';

export default {
  data() {
    return {
      product: {},
      isLoading: false,
      mainImg: '',
      classIndex: -1,
      productNum: 1,
      likedData: [],
    };
  },
  components: {
    OtherCard,
  },
  mounted() {
    // 最愛
    this.$emitter.on('push-like', () => {
      this.getLikes();
    });
  },
  methods: {
    // 判斷css狀態
    isLiked() {
      if (this.likedData.indexOf(this.product.id) > -1) {
        return true;
      }
      return false;
    },
    // 取得localStorage計算最愛數量
    getLikes() {
      this.likedData = JSON.parse(localStorage.getItem('liked'));
    },
    // 推送資料到localStorage
    liked() {
      const data = localStorage.getItem('liked');
      const dataArry = JSON.parse(data) ?? [];
      const a = dataArry.indexOf(this.product.id);
      if (a > -1) {
        dataArry.splice(a, 1);
        this.likedData = dataArry;
      } else {
        dataArry.push(this.product.id);
        this.likedData = dataArry;
      }
      localStorage.setItem('liked', JSON.stringify(this.likedData));

      //   吐司回覆
      this.$emitter.emit('push-like', {
        style: 'success',
        title: '關注',
        content: '已更新最愛標籤',
      });
    },

    getproduct() {
      this.isLoading = true;
      const { id } = this.$route.params;
      const Api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/product/${id}`;
      this.$http
        .get(Api)
        .then((res) => {
          this.product = res.data.product;
          if (this.product.images) {
            const mainImg = this.product.images[0];
            this.mainImg = mainImg;
            this.classIndex = 0;
          } else {
            this.mainImg = this.product.imageUrl;
          }
          this.isLoading = false;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    updateCart() {
      this.isLoading = true;
      const cart = {
        product_id: this.product.id,
        qty: this.productNum,
      };

      const Api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(Api, { data: cart })
        .then((res) => {
          //   吐司回覆
          this.$emitter.emit('push-cart', {
            style: 'success',
            title: res.data.message,
            content: res.data.message,
          });

          this.isLoading = false;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    pickPic(i, index) {
      this.classIndex = index;
      this.mainImg = i;
    },
  },
  watch: {
    productNum(n, o) {
      if (this.productNum <= 0 || this.productNum > 4) {
        //  吐司訊息
        this.productNum = o;
      }
    },
    //  路由改變時強制刷新
    $route() {
      this.getproduct();
    },
  },
  created() {
    this.getproduct();
  },
};
</script>

<style lang="scss" scoped>
.mainimg {
  img {
    width: 100%;
  }
  .propimg {
    position: relative;
    img {
      width: 100%;
    }

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($color: white, $alpha: 0.6);
      opacity: 1;
    }
    .noneOverlay {
      opacity: 0;
    }
  }
}
// .bottxt {
//   padding-top: 1rem;
//   background-color: rgba($color: blue, $alpha: 0.2);
// }

.like {
  font-size: 2.5rem;
  cursor: pointer;
  i {
    margin-right: 1rem;
  }
  color: red;
}
// .liked {
//   color: red;
// }
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
input[type='number'] {
  -moz-appearance: textfield;
}
</style>
